import Layout from "../components/Layout";

const KillerProfile = props => {
  return (
    <Layout>
      <div>
        <img src={props.img} alt="" />
      </div>
    </Layout>
  );
};

KillerProfile.getInitialProps = async context => {
  const killer = context.query;

  return {
    killer
  };
};

export default KillerProfile;
